.civil {
    padding: 5px 0;
    text-align: center;
    height: 250px;
    width: 100%;
}

p {
    font-weight: bolder;
    font-family: Arial;
}

a {
    color: black;
}

a:hover {
    text-decoration: none;
    color: black;
}

ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: #333333;
    text-align: center;
}
  
li {
    display: inline-block;
    padding: 4px;
}

.made {
    width: 100%;
    font-size: smaller;
    font-family: Arial;
    font-weight: bolder;
    border: none;
    background-color: white;
}

input, textarea {
    width: 100%;
    border: none;
    border-bottom: 1px solid darkgray;
    font-size: medium;
    text-indent: 5px;
    padding: 3px 0;
}

input::placeholder, textarea::placeholder {
    color: darkgray;
    font-size: small;
}


@media screen and (max-width: 480px) {
    .civil {
        height: 175px;
    }
    p {
        font-size: smaller;
    }
}